import React from 'react';
import { Row, Col, Card, Table, Modal, Button } from 'react-bootstrap';

import Aux from "../../hoc/_Aux";

import { getVendors, deleteVendors } from '../../api/inventory/vendorApi';
import IsEmpty from '../../App/components/Reusables/IsEmpty';
import Loader from '../../App/components/Reusables/Loader';
import DeleteModal from '../../App/components/Reusables/DeleteModal';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import isEmpty from '../../validation/isEmpty';
import Spinner from '../../App/components/Reusables/Spinner';
import Paging from '../../App/components/Reusables/Paging';
import VendorsFilter from '../../App/components/SearchFilters/VendorsFilter';
import { getVendorsSearchFilterObject } from '../../helpers/getSearchFilterObject';
import { getStartEnd } from '../../helpers/getStartEnd';

const Vendors = (props) => {
    const [vendorsArr, setVendorsArr] = React.useState([]);
    const [loading, setLoading] = React.useState(true)
    const [deleteData, setDeleteData] = React.useState({ showModal: false, vendorId: null })
    const [instanceCount, setInstanceCount] = React.useState(0)

    React.useEffect(() => {
        const controller = new AbortController();
        getVendors({
            "action": "get",
            "filter": "none",
            ...getVendorsSearchFilterObject(props),
            ...getStartEnd(props)
        }, controller.signal)
            .then((res) => {
                if (res.json.status) {
                    console.log(res.json.vendors);
                    setVendorsArr(res.json.vendors);
                    setInstanceCount(res.json.count)
                    setLoading(false);
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            })
            .catch((err) => {
                console.log(err)
            });
        return () => controller.abort();
    }, []);



    const _triggerDeleteModal = (id) => {
        setDeleteData({ showModal: true, vendorId: id });
    }

    const _delete = (idToBeDeleted) => {
        deleteVendors({ action: 'delete', vendors_id: [idToBeDeleted] })
            .then(async (res) => {
                console.log(res.json)
                if (res.json.status) {
                    const deletedVendor = vendorsArr.find(item => item.id === idToBeDeleted);
                    let tempArr = vendorsArr;
                    tempArr.splice(tempArr.indexOf(deletedVendor), 1);
                    await setVendorsArr([...tempArr])
                    await setDeleteData({ ...setDeleteData, showModal: false, vendorId: null })
                    Jhilikka.fire({ icon: 'success', title: 'Vendor deleted successfully!', background: '#dffff0' })
                }
            })
            .catch((err) => {
                console.log(err)
            });
    }


    const VendorsList = vendorsArr.map((item, index) => {
        return (
            <tr key={index}>
                <th scope="row">{item.id}</th>
                <td
                    onClick={() => {
                        props.history.push('/vendor/' + item.id)
                    }}
                    style={{ cursor: 'pointer', color: '#55588b' }}>{item.first_name} {item.last_name}</td>
                <td>{item.tax_number}</td>
                <td>{item.phone1}</td>
                <td>{item.country_detail.name}</td>
                <td>{item.company}</td>
                <td>{item.is_active ? 'Yes' : 'No'}</td>
                <td>
                    <i
                        className="feather icon-edit text-c-green f-19 m-r-10"
                        style={{ cursor: 'pointer' }}
                        onClick={() => props.history.push('/vendor/update/' + item.id)}
                    />
                    <i
                        className="feather icon-trash-2 text-c-red f-19 m-r-5"
                        style={{ cursor: 'pointer' }}
                        onClick={() => _triggerDeleteModal(item.id)}
                    />
                </td>
            </tr>
        )
    })

    return (
        <Aux>
            <Row className="d-flex justify-content-between pageHead" >
                <h4 className="m-b-10" >Vendors</h4>
                <div>
                    <button
                        className="btn btn-primary btn-sm shadow-2 mb-4"
                        onClick={() => props.history.push(`vendor/create`)}
                    > <i className="fa fa-plus text-c-white f-15 m-r-5" />Add New</button>
                </div>

            </Row>
            <Row>
                <Col>

                    <Card>
                        <Card.Body>
                            <VendorsFilter />
                            <Table striped responsive>
                                <thead>
                                    <tr>
                                        <th>#</th>
                                        <th>Name</th>
                                        <th>Tax Number</th>
                                        <th>Phone No.</th>
                                        <th>Country</th>
                                        <th>Company</th>
                                        <th>Enabled</th>
                                        <th></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {VendorsList || ''}
                                </tbody>
                            </Table>
                            {isEmpty(vendorsArr) && !loading ? <><IsEmpty>Data not available</IsEmpty><br /><br /></> : ''}
                            {loading ? <Spinner /> : null}
                            <Paging instanceCount={instanceCount} />
                        </Card.Body>
                    </Card >
                </Col>

                <DeleteModal
                    show={deleteData.showModal}
                    onCancel={() => setDeleteData({ ...deleteData, showModal: false })}
                    onDelete={() => _delete(deleteData.vendorId)}
                />
            </Row>
        </Aux>
    );
}

export default Vendors;